html,
body {
    height: 100%;
}

body {
    background-color: #939393;
    color: #f5f5f5;
    font-family: "Lato", sans-serif;
}

header {
    margin-bottom: 30px;
}

header,
footer {
    background-color: #006400;
    color: #eeeeee;
    height: 50px;
    width: 100%;
}

footer {
    text-align: center;
    padding-top: 5px;
    font-size: 18px;
}

footer a {
    color: white;
}

footer a:hover,
footer a:visited {
    color: white;
}

i.info {
    margin-right: 10px;
}

.idcol {
    width: 5%;
    text-align: center;
}

.numbercol {
    text-align: center;
}

.buttons {
    width: 4%;
    font-size: 10px;
}

.userimg {
    width: 40px;
    border: 2px solid #006400;
    border-radius: 4px;
    max-height: 40px;
    margin-right: 4px;
}

tr.top10 {
    font-size: 18px;
}

th {
    background-color: #eef0ee;
}

.sortable {
    color: #336033;
    text-decoration: underline;
    text-align: center;
    cursor: pointer;
}

table {
    width: 100%;
}

th a,
th a:hover {
    color: #336033;
}

tr.top10 td {
    vertical-align: middle;
    text-align: center;
}

td.count {
    text-align: center;
}

td.lastchecked {
    font-size: 12px;
}

table.table-striped {
    background-color: #f0f0f0;
    color: #333;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    border-collapse: inherit;
    border: 1px solid #351f0b;
}

table.table-striped tr {
    border: 0;
}

#header {
    background-color: #006400;
    text-align: center;
    padding: 8px;
    border-color: #351f0b;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

#header h3 {
    color: #f5f5f5;
    margin: 0;
    font-size: 25px;
    font-weight: 300;
}

.btn {
    font-weight: 400;
}


